let queries = {
  getProductsWithImages: numberOfProducts => {
    return `query	{
      products(first:${numberOfProducts})	{
        edges {
          node {
            id
            title
            images(first: 1) {
              edges {
                node {
                  id
                }
              }
            }
            priceRange {
              maxVariantPrice {
                amount
              }
            }
          }
        }
      }
    }`;
  },
  getShopName: () => {
    return `query	{
      shop {
        name
      }
    }
    `;
  }
};

export default queries;
